<template>
  <popup icon="clock" :title="'Payment #' + value.id" v-if="value" :show="value.visible">
    <div class="payment-popup">
      <div class="title">Send exactly {{ value.amount }} at</div>
      <Tooltip text="Address copied!" theme="white" :index="value.address" />
      <div class="address" @click="copyPaymentAddress(value.address)">
        {{ value.address }}
      </div>
      <div class="wrap">
        <div class="desc">
          Your payment will be completed after confirmation by the network.
          Confirmation time may vary and depends on the Commission
        </div>
        <div class="qr">
          <div class="qrcode" v-html="value.qr"></div>
        </div>
      </div>
    </div>
  </popup>
</template>

<script>
import Popup from '@/components/partial/Popup.vue'
import Tooltip from '@/components/partial/Tooltip.vue'

import qrcode from 'qrcode-generator'

export default {
  name: 'payment',
  components: {
    Popup,
    Tooltip,
  },
  props: {
    value: {
      type: Object,
      default: () => ({
        visible: false,
        id: 0,
        address: '',
        amount: '',
        qr: a => {
          var qr = qrcode(0, 'L')
          qr.addData(a)
          qr.make()
          return qr.createImgTag(4, 1)
        },
      }),
    },
  },
  methods: {
    copyPaymentAddress(a) {
      this.$root.$emit('tooltip:once', a)
      this.$copy(a)
    },
  },
}
</script>

<style lang="sass" scoped>

@import "../../../../scss/smart-grid"
@import "../../../../scss/variables"

.payment-popup
  display: grid
  justify-items: center
  padding-bottom: 20px

  .title
    color: white
    font-size: 1.4rem

  .address
    display: grid
    align-content: center
    margin: 10px 0px
    width: 80%
    font-size: .9rem
    font-weight: bold
    outline: none
    border: none
    color: $light-blue
    background-color: $cyan-alpha
    border-radius: 5px
    height: 30px
    text-align: center
    cursor: pointer
    transition: all .15s

    &:hover
      background-color: $cyan
      color: white

  .wrap
    width: 80%
    height: 120px
    align-items: center
    display: grid
    grid-template-columns: 1.5fr 1fr

    .desc
      text-align: left
      display: grid
      align-content: end
      width: 100%
      color: $cyan
    .qr
      display: grid
      justify-content: center
      align-content: center
      height: 150px
      width: 100%

      img
        width: 122px
        height: 122px
</style>
